<template>
  <div style="width:100%;height:100%">
    <div class="menu-top">
      <el-button class="el-icon-arrow-left return" @click=" ret()"></el-button>团队详情
    </div>
    <div class="table-top radius_8">
      <div class="table-top-con">
        <div class="table-top-con-span">
          <span>团队名称</span>
          <p>{{topData.team_name?topData.team_name:'无'}}</p>
        </div>
        <div class="table-top-con-span">
          <span>客户数量</span>
          <p>{{topData.user_nums?topData.user_nums:0}}</p>
        </div>
        <!--  -->
        <div class="table-top-con-span">
          <span>创建时间</span>
          <p>{{topData.create_time?topData.create_time:'无'}}</p>
        </div>
        <div class="table-top-con-span">
          <span>项目</span>
          <p>{{topData.build_name?topData.build_name:'无'}}</p>
        </div>
        <div class="table-top-con-span">
          <span>团队人数</span>
          <p>{{topData.team_nums?topData.team_nums:0}}</p>
        </div>
        <div class="table-top-con-span">
          <span>负责人</span>
          <p>{{topData.leader?topData.leader:'无'}}</p>
        </div>
        <div class="table-top-con-span">
          <span>联系方式</span>
          <p>{{topData.saller_tel?topData.saller_tel:'无'}}</p>
        </div>
      </div>
    </div>

    <div class="menu-small-top">
      <span class="font_size_weight_600">团队列表</span>
      <!-- <el-button class="but" @click="add" style="float:right" type="primary">添加</el-button> -->
    </div>
    <div class="table-btn">
      <template>
        <el-table :data="tableData" style="width: 100%" class="font_size_16">
          <el-table-column label="序号" width="100" align="center" type="index" :index="indexMethod"></el-table-column>
          <el-table-column prop="saller_name" label="成员" width="260"></el-table-column>
          <el-table-column prop="saller_tel" label="账号" width="260"></el-table-column>
          <el-table-column prop="nums" width="260" label="客户数量"></el-table-column>
          <el-table-column prop="title" width="260" label="职位"></el-table-column>
          <el-table-column prop="entry_date" width="260" label="加入时间"></el-table-column>
          <el-table-column prop="build_name" width="260" label="负责项目"></el-table-column>
        </el-table>
      </template>
      <div class="page">
        <el-pagination
          background
          layout="total,prev, pager, next,jumper"
          @current-change="handleCurrentChange"
          :current-page="sel.page_num"
          :page-size="size"
          :page-count="count"
          :total="total"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { getTeam } from "@/api/getData";
export default {
  name: "cus",

  data() {
    return {
      sel: {
        team_id: 0,
        pre_page: 10, //每页条数
        page_num: 1 //第几页
      },
      topData: {},
      value: "",
      radio: "",
      time: "",
      isDia: false,
      tabName: "创建",
      size: 8, //每页条数
      count: 100, //总页数
      total: 100, //总条数
      tableData: []
    };
  },
  components: {},
  computed: {},
  created() {
    // this.handleListener();
    // this.changeDate();
  },
  activated() {
    // this.handleListener();
  },
  deactivated() {
    // window.removeEventListener('resize', this.renderChart);
    // bus.$off('collapse', this.handleBus);
  },
  async mounted() {
    this.sel.team_id = this.$route.query.team_id;

    // alert( this.$route.params.sel);
    this.changeDate();
  },
  methods: {
    async changeDate() {
      // 查询数据
      const res = await getTeam(this.sel);
      //  console.log(res);
      if (res.code != 0) {
        this.$message({
          type: "error",
          message: res.msg
        });
        return;
      }
      this.topData = res.data;
      this.tableData = res.data.list;
      this.total = res.data.total;
    },
    indexMethod(index) {
      return index + 1 * 1;
    },
    closeSetPas() {
      this.isDia = false;
    },
    handleCurrentChange(val) {
      this.sel.page_num = val;
      this.changeDate();
    },
    ret() {
      // this.$route.params.sel

      this.$router.push({ path:"/team",query: { sel:this.$route.params.sel}});
    }
  }
};
</script>


<style scoped>
.grid-content {
  background-color: black;
}
.table-top {
  height: 27%;
  /* display: flex; */
  /* justify-content: space-around; */
}
.table-top-con {
  width: 80%;
  height: 80%;
  padding: 20px;
  /* background: red; */
  display: flex;
  flex-wrap: wrap;
}
.table-top-con-span {
  width: 25%;
  height: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.table-top-con-span span {
  font-size: 18px;
  color: rgba(116, 134, 216, 1);
}
.table-top-con-span p {
  font-size: 18px;
  color: rgba(131, 208, 205, 1);
}
.table-top-left {
  width: 70%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.table-top-rig {
  width: 25%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.table-top-left-top,
.table-top-left-btn {
  height: 50%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.div_p {
  width: 26%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.inp {
  width: 200px;
}

.page {
  display: flex;
  height: 38px;
  margin-right: 10px;
  align-items: center;
  flex-direction: row-reverse;

  /* float: right; */
}
/* 修改 */
.upPaw {
  width: 90%;
  margin-left: 4%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.upPaw div span {
  font-size: 20px;
  /* font-weight: 600; */
  width: 35%;
}
.upPaw_pa {
  width: 100%;
  height: 30%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 20px;
}
.set-role {
  margin-top: 20px;
  width: 100%;
  height: 30%;
  /* background: rosybrown; */
  display: flex;
}
.set-role span {
}
.set-role-right {
  vertical-align: middle;
  width: 200px;
  /* background: royalblue; */
}
.return {
  font-size: 24px;
  /* height: 40px; */
  padding: 0px;
  /* vertical-align: middle; */
  background: rgba(255, 255, 255, 0.2);
  margin-right:10px ;
}
</style>
<style >
.el-table__header {
  padding: 5px 0;
}
.el-table__body-wrapper .el-table__row td {
  padding: 5px 0;
}
.el-input__inner {
  height: 37px;
}
.el-input__icon {
  line-height: 37px;
}
.el-radio {
  margin-top: 5px;
}
</style>



